.body--background {
    background-color: #ededed;
}

.cake-header--carrot {
    background-color: white;
    box-shadow: 0 2px 2px rgba(0,0,0,0.02), 0 2px 2px rgba(0,0,0,0.01);
}

.gel-pica-carrot {
    font-size: 16px !important;
    line-height: 20px;
}

.carrot--heading {
    color: orange;
}

.gel-pica-carrot-bold {
    font-weight: bold;
}

.package--background {
    padding-top: 16px;
    padding-bottom: 16px;
    margin-top: 0px;
    margin-bottom: 40px;
    box-shadow: 0 2px 2px rgba(0,0,0,0.02), 0 2px 2px rgba(0,0,0,0.01);

    background-color: white;
}

.lead-item {
    margin-bottom: 16px;
}

.lead-item__summary {
    color: #5a5a5a;
}

.lead-item__title {
    margin-top: 8px;

    @include mq($from: gel-bp-m) {
        margin-top: 0px;
    }

    @include mq($from: gel-bp-l) {
        margin-top: 8px;
    }

    @include mq($from: gel-bp-xxl) {
        margin-top: 0px;
    }
}

.carrot-item {
    margin-bottom: 08px;
    position: relative;
    padding-top: 08px;

    @include mq($from: gel-bp-m) {
        margin-bottom: 0px;
        padding-top: 0px;
    }
}

.carrot-item--large-list {
    @include mq($from: gel-bp-l) {
        padding-bottom: 08px;
        padding-top: 08px;
    }

    &:before {
        @include mq($from: gel-bp-l) {
            position: absolute;
            right: 0;
            top: 0;
            left: 16px;

            display: block;
            border: 0 solid #dbdbdb;
            border-top-width: 1px;

            content: '';
        }
    }

    &:first-child {
        &:before {
            border-top-width: 0px;
        }
    }

    &:first-child {
            padding-top: 0px;
        }
}

.carrot-item__body {
    margin-top: 0px;

    @include mq($from: gel-bp-m) {
        margin-top: 8px;
    }

    @include mq($from: gel-bp-l) {
        margin-top: 8px;
        padding-right: 8px;
    }
}

.carrot-item--keyline {

    &:before {
        @include mq($until: gel-bp-m) {
            position: absolute;
            right: 0;
            top: 0;
            left: $gel-spacing-unit;

            display: block;
            border: 0 solid #dbdbdb;
            border-top-width: 1px;

            content: '';
        }
    }

    &:first-child {
        &:before {
            border-top-width: 0px;
        }
    }
}

.carrot-item__summary {
    display: none;
    color: #5a5a5a;


    @include mq($from: gel-bp-m) {
        display: inline-block;
        padding-right: 8px;
    }
}

.meta {
    color: #5a5a5a;
}

.carrot-item-text {
    margin-bottom: 16px;
    padding-top: 16px;
    position: relative;

    @include mq($from: gel-bp-m) {
        margin-bottom: 0px;
        padding-top: 0px;
        border-right: 1px solid #ededed;

        &:nth-child(3) {
           border-right: 0px;
        }
    }
}

.carrot-item-text__summary {
    color: #5a5a5a;
    padding-right: 8px;
}

.carrot-item-text__title {
    padding-right: 8px;
}

.carrot-item-row--vertical {
    margin-bottom: 24px;
}

.carrot-item-row--horizontal {

    @include mq($from: gel-bp-m) {
        margin-bottom: 16px;
    }
}

.carrot-item-row--image {
    @include mq($from: gel-bp-m) {
        margin-bottom: 12px;
    }

    @include mq($from: gel-bp-xxl) {
        margin-bottom: 0px;
    }
}

.carrot-item-6--image {
    @include mq($from: gel-bp-xxl) {
        margin-bottom: 12px;
    }
}

.section-tag {
    color: #006D36;
}

.section-keyline {
    color: #dbdbdb;
}

.functionality {
    background-color: orange;
    padding: 16px;
    height: 100%;
    margin-top: 16px;

    @include mq($from: gel-bp-xxl) {
        margin-top: 0px;
    }
}
